/*
* mixins
*/
/*
* animations
*/
.popup-fade-enter,
.popup-fade-leave-active {
  opacity: 0;
}
.popup-fade-enter.header-dropdown-settings,
.popup-fade-leave-active.header-dropdown-settings {
  transform: scale(0.9);
}
/*
* processing loading animation
*/
.content-loading {
  margin: 10px 0;
  width: 100%;
  height: 49px;
  text-align: center;
}
.processing-loader {
  width: 49px;
  height: 49px;
  border-radius: 50%;
  margin: 0 auto;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  background: #a4e0f4;
}
.processing-loader,
.processing-loader:before,
.processing-loader:after {
  animation: 1.15s infinite ease-in-out;
  -o-animation: 1.15s infinite ease-in-out;
  -ms-animation: 1.15s infinite ease-in-out;
  -webkit-animation: 1.15s infinite ease-in-out;
  -moz-animation: 1.15s infinite ease-in-out;
}
.processing-loader:before,
.processing-loader:after {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
}
.processing-loader {
  animation-name: processing-loader;
  -ms-animation-name: processing-loader;
  -webkit-animation-name: processing-loader;
  -moz-animation-name: processing-loader;
}
@keyframes processing-loader {
from {
    transform: scale(0);
    opacity: 1;
}
to {
    transform: scale(1);
    opacity: 0;
}
}
.editor__container {
  height: calc(100vh - 54px);
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  background: #ffffff;
}
.editor__controls {
  max-height: calc(100vh - 54px);
  overflow: auto;
}
.editor__box {
  position: relative;
  padding: 15px;
  background: #ffffff;
  text-align: left;
  border-bottom: solid 1px #f0f0f2;
}
.editor__box-title {
  cursor: pointer;
  font-weight: bold;
  margin-bottom: 10px;
}
.editor__box-title:hover {
  color: #27292b;
}
.editor__box:hover {
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
  z-index: 10;
}
.editor__box-input,
.editor__box-textarea {
  width: 100%;
  height: 30px;
  margin: 10px 0;
  font-size: 14px;
  padding: 5px 8px;
  border-radius: 3px;
  color: #4e5156;
  border: solid 1px #e4e5e7;
  background: #f8f8fa;
  outline: none;
}
.editor__box-textarea {
  height: 80px;
  line-height: 20px;
  resize: none;
}
.editor__box-label {
  margin-top: 5px;
  font-size: 15px;
  min-width: 100px;
  display: inline-block;
}
.editor__box-img {
  width: 100%;
}
.editor__box button {
  padding: 4px 10px;
  font-size: 15px;
  margin-top: 10px;
  border-radius: 3px;
  background: #e4e5e7;
  border: 1px solid #c5c7c9;
  cursor: pointer;
  transition: background ease 0.3s;
}
.editor__box button:hover {
  background: #d1d3d5;
}
.editor__box-toggle {
  position: absolute;
  top: 18px;
  right: 14px;
  background: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='40' height='45' version='1.1'><polyline fill='rgb(50,55,60)' points='0,10 40,10 20,45'/></svg>") left top / 11px auto no-repeat;
  height: 11px;
  width: 11px;
  transform-origin: center;
  transition: all 0.3s ease;
  opacity: 0.4;
}
.editor__box-toggle.closed {
  transform: rotate(180deg);
}
.editor__box-toggle:hover {
  cursor: pointer;
  opacity: 0.8;
}
.editor__box-color-preview {
  margin: 5px 0;
  padding: 5px 10px;
  width: 80px;
  cursor: pointer;
  font-size: 14px;
  border-radius: 3px;
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.1);
}
.editor__box-color-preview:hover {
  box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.2);
}
.editor__box-color {
  width: 100%;
  max-width: 255px;
  margin: 10px 0;
}
.editor__box-upload {
  position: relative;
  width: 100%;
  min-height: 120px;
  font-size: 14px;
  color: #61666b;
  padding: 10px;
  margin: 5px 0;
  border-radius: 3px;
  border: solid 1px #e4e5e7;
  background-color: #f8f8fa;
  transition: background-color ease 0.3s;
}
.editor__box-upload:hover {
  background-color: #f3f3f5;
  border: 1px solid #d1d3d5;
}
.editor__box-text {
  font-size: 14px;
}
.editor__box-error {
  font-size: 12px;
  color: red;
}
.camera-controls {
  position: absolute;
  bottom: 10px;
  left: 10px;
  z-index: 100;
}
.btn-waypoint,
.btn-toggle-play {
  background-color: rgba(255, 255, 255, 0.9);
  box-sizing: border-box;
  display: block;
  border-radius: 3px;
  font-size: 14px;
  color: #333;
  height: 30px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
  transition: all ease 0.3s;
  cursor: pointer;
  margin: 0 5px 5px 0;
  padding: 0 10px;
  text-decoration: none;
  border: none;
  outline: none;
}
.btn-waypoint:hover,
.btn-toggle-play:hover {
  background-color: #ffffff;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
}
.btn-toggle-play {
  display: none;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 20px 20px;
  width: 35px;
  height: 35px;
  background-image: url('https://spaces-static.archilogic.com/build/170920-121718-65fe40d/e3d/ui/ui-play-black.svg');
}
/* display play button only if there are waypoints */
.btn-waypoint + .btn-toggle-play {
  display: block;
}
.btn-toggle-play.playing {
  background-image: url('https://spaces-static.archilogic.com/build/170920-121718-65fe40d/e3d/ui/ui-pause-black.svg');
}
.camera-mode > .btn {
  width: 40px;
  height: 40px;
  background: white;
  border-radius: 50%;
  display: inline-block;
  cursor: pointer;
  margin: 5px 5px 0 0;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 30px 30px;
  transition: all ease 0.3s;
}
.camera-mode > .btn.camera {
  background-image: url('https://spaces-static.archilogic.com/build/170920-121718-65fe40d/e3d/ui/icon-camera-black.svg');
}
.camera-mode > .btn.bird {
  background-image: url('https://spaces-static.archilogic.com/build/170711-151653-c61293f/e3d/ui/icon-birdview-black.svg');
}
.camera-mode > .btn.person {
  background-image: url('https://spaces-static.archilogic.com/build/170711-151653-c61293f/e3d/ui/icon-person-black.svg');
}
.camera-mode > .btn.camera.active {
  background-image: url('https://spaces-static.archilogic.com/build/170920-121718-65fe40d/e3d/ui/icon-camera-blue.svg');
}
.camera-mode > .btn.bird.active {
  background-image: url('https://spaces-static.archilogic.com/build/170711-151653-c61293f/e3d/ui/icon-birdview-blue.svg');
}
.camera-mode > .btn.person.active {
  background-image: url('https://spaces-static.archilogic.com/build/170711-151653-c61293f/e3d/ui/icon-person-blue.svg');
}
.camera-mode > .btn:hover {
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
}



