/*
* mixins
*/
/*
* animations
*/
.popup-fade-enter,
.popup-fade-leave-active {
  opacity: 0;
}
.popup-fade-enter.header-dropdown-settings,
.popup-fade-leave-active.header-dropdown-settings {
  transform: scale(0.9);
}
/*
* processing loading animation
*/
.content-loading {
  margin: 10px 0;
  width: 100%;
  height: 49px;
  text-align: center;
}
.processing-loader {
  width: 49px;
  height: 49px;
  border-radius: 50%;
  margin: 0 auto;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  background: #a4e0f4;
}
.processing-loader,
.processing-loader:before,
.processing-loader:after {
  animation: 1.15s infinite ease-in-out;
  -o-animation: 1.15s infinite ease-in-out;
  -ms-animation: 1.15s infinite ease-in-out;
  -webkit-animation: 1.15s infinite ease-in-out;
  -moz-animation: 1.15s infinite ease-in-out;
}
.processing-loader:before,
.processing-loader:after {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
}
.processing-loader {
  animation-name: processing-loader;
  -ms-animation-name: processing-loader;
  -webkit-animation-name: processing-loader;
  -moz-animation-name: processing-loader;
}
@keyframes processing-loader {
from {
    transform: scale(0);
    opacity: 1;
}
to {
    transform: scale(1);
    opacity: 0;
}
}
header {
  width: 100%;
  height: 54px;
  background: #fff;
  padding: 10px 0 10px 15px;
  text-align: left;
  border-bottom: 1px solid #e4e5e7;
}
header h1 {
  margin: 0;
  font-size: 22px;
  line-height: 34px;
  height: 34px;
  display: inline-block;
  vertical-align: top;
}
.header__logo {
  width: 32px;
  height: 34px;
  background: 0 -5px no-repeat;
  background-image: url('../assets/3dio-app-creator.svg');
  background-size: 40px;
  display: inline-block;
  margin-right: 10px;
}



