/*
* mixins
*/
/*
* animations
*/
.popup-fade-enter,
.popup-fade-leave-active {
  opacity: 0;
}
.popup-fade-enter.header-dropdown-settings,
.popup-fade-leave-active.header-dropdown-settings {
  transform: scale(0.9);
}
/*
* processing loading animation
*/
.content-loading {
  margin: 10px 0;
  width: 100%;
  height: 49px;
  text-align: center;
}
.processing-loader {
  width: 49px;
  height: 49px;
  border-radius: 50%;
  margin: 0 auto;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  background: #a4e0f4;
}
.processing-loader,
.processing-loader:before,
.processing-loader:after {
  animation: 1.15s infinite ease-in-out;
  -o-animation: 1.15s infinite ease-in-out;
  -ms-animation: 1.15s infinite ease-in-out;
  -webkit-animation: 1.15s infinite ease-in-out;
  -moz-animation: 1.15s infinite ease-in-out;
}
.processing-loader:before,
.processing-loader:after {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
}
.processing-loader {
  animation-name: processing-loader;
  -ms-animation-name: processing-loader;
  -webkit-animation-name: processing-loader;
  -moz-animation-name: processing-loader;
}
@keyframes processing-loader {
from {
    transform: scale(0);
    opacity: 1;
}
to {
    transform: scale(1);
    opacity: 0;
}
}
a-scene,
.scene-container {
  width: 100%;
  height: 100%;
  max-width: 100%;
}
.a-enter-vr.embedded {
  display: none;
}
.logo {
  position: absolute;
  top: 20px;
  left: 20px;
  right: auto;
}
.logo--align-right {
  left: auto;
  right: 20px;
}
.logo__img {
  max-width: 100%;
  max-height: 100%;
}
.screenshot-preview {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -100;
}



