/*
* mixins
*/
/*
* animations
*/
.popup-fade-enter,
.popup-fade-leave-active {
  opacity: 0;
}
.popup-fade-enter.header-dropdown-settings,
.popup-fade-leave-active.header-dropdown-settings {
  transform: scale(0.9);
}
/*
* processing loading animation
*/
.content-loading {
  margin: 10px 0;
  width: 100%;
  height: 49px;
  text-align: center;
}
.processing-loader {
  width: 49px;
  height: 49px;
  border-radius: 50%;
  margin: 0 auto;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  background: #a4e0f4;
}
.processing-loader,
.processing-loader:before,
.processing-loader:after {
  animation: 1.15s infinite ease-in-out;
  -o-animation: 1.15s infinite ease-in-out;
  -ms-animation: 1.15s infinite ease-in-out;
  -webkit-animation: 1.15s infinite ease-in-out;
  -moz-animation: 1.15s infinite ease-in-out;
}
.processing-loader:before,
.processing-loader:after {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
}
.processing-loader {
  animation-name: processing-loader;
  -ms-animation-name: processing-loader;
  -webkit-animation-name: processing-loader;
  -moz-animation-name: processing-loader;
}
@keyframes processing-loader {
from {
    transform: scale(0);
    opacity: 1;
}
to {
    transform: scale(1);
    opacity: 0;
}
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #3d4044;
  margin-top: 0;
  font-size: 16px;
  letter-spacing: 0.3px;
}
* {
  box-sizing: border-box;
}
body {
  padding: 0;
  margin: 0;
}
ul {
  padding: 0;
  list-style-type: none;
}
button {
  border: none;
  outline: none;
  font-size: 16px;
}



